.intro{
    background-color: #fff;
    /* background: url('../../../../public/images/4683.jpg'); */
    /* background-image: url('/images/388.jpg'); */
    background-repeat: no-repeat;
    background-size: cover;
    /* padding: 35px; */
    color: #007bff;
    position: relative;
}

.intro img{
    width: 100vw;
    height: 30vh;
    opacity: 1;
    filter: drop-shadow(8px 8px 10px);
}

.intro h1{
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    color: #ffffff;
    font-size: 2.2em;
}

.intro p{
    margin: 0;
}
.intro .intro-content  .header-intro{
    margin-left: 20px
}

.intro .intro-content  .small-header-intro{
    margin-left: 30px;
    font-style: oblique;
    font-weight: bold;
    color: #007bff

}

.intro .intro-content p{
    margin-left: 40px;
}

h4 , h1{
    color: #007bff
}
.margin-content{
    margin: 20px;
}

@media only screen and (max-width: 489px) {
    .intro .intro-content{
        background: #ffffffeb;
        position: absolute;
    }
}

@media only screen and (min-width: 490px) {
    .intro .intro-content{
        background: #61cbe814;
        position: absolute;
      
    }

    .intro .intro-content .margin-content{
        background: #ffffff;
        margin: 30px;
        padding: 20px;
    }
}